<template>
  <div class="exclusive full-screen">
    <pem-header left="back" title="独家首发"></pem-header>
    <div class="page-panel">
      <pem-scroll-view
        class="scroll-view"
        :data='list'
        :pullup=true
        :pulldown=true
        @pullDownRefresh="listRefresh">
        <div class="scroll-view-container">
          <div class="scroll-view-pulldown">下拉刷新...</div>
            <pem-project-list :data="list" @alert="alert"/>
          <div class="scroll-view-pullup">目前就这么多了</div>
        </div>
      </pem-scroll-view>
      <pem-loading v-show="isLoading"></pem-loading>
    </div>
    <pem-download-bar title="项目工场" desc="找项目、找投资人信息，就用项目工场" />
    <pem-dialog ref='alert' :text="alertText" :confirmBtnText="alertBtnText" :type="alertType" @confirm="alertCallback"></pem-dialog>
  </div>
</template>

<script type="text/ecmascript-6">
import PemHeader from 'components/header/header'
import PemProjectList from 'components/pem-project-list/pem-project-list'
import PemDownloadBar from 'components/download-bar/download-bar'
import PemScrollView from 'components/scroll-view/scroll-view'
import PemDialog from 'components/dialog/dialog'
import PemLoading from 'components/pem-loading/pem-loading'
import { MiniSiteApi, ImgPath } from 'api'

export default {
  data () {
    return {
      list: [],
      alertText: '',
      alertType: '',
      alertBtnText: '',
      alertBtnCallback: null,
      isLoading: true
    }
  },
  components: {
    PemHeader,
    PemProjectList,
    PemDownloadBar,
    PemScrollView,
    PemDialog,
    PemLoading
  },
  created () {
    this._initWXShare()
    setTimeout(() => {
      this._initData()
    }, 20)
  },
  methods: {
    _initWXShare () {
      this.wxShare({
        title: '为您推荐项目工场',
        content: '我正在使用项目工场，有料的BP、有趣的VC都在这儿，一起来看项目吧！',
        linkUrl: window.location.origin
      })
    },
    _initData () {
      this.getExclusivereCommend()
    },
    listRefresh () {
      if (!this.isLoading) {
        this.getExclusivereCommend()
      }
    },
    alert (text, options = {}) {
      this.alertText = text
      this.alertType = options.type || 'alert'
      this.alertBtnText = options.btn || '确定'
      this.alertBtnCallback = options.callback || null
      this.$refs.alert.show()
    },
    alertCallback () {
      this.alertBtnCallback && this.alertBtnCallback()
    },
    getExclusivereCommend () {
      this.isLoading = true
      this._getExclusivereCommend(data => {
        this.isLoading = false
        if (+data.resultcode === 1) {
          this.list = data.resultList.map(item => {
            item.logo = ImgPath + item.logo
            if (item.iconList) {
              item.iconList = item.iconList.map(icon => {
                icon.imgurl = ImgPath + icon.imgurl
                return icon
              })
            }
            return item
          })
        }
      })
    },
    _getExclusivereCommend (callback) {
      MiniSiteApi.getExclusivereCommend().then(data => {
        callback(data)
      })
    }
  }
}
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  .exclusive
    font-size: 0.24rem
</style>



